import React from 'react';
import  "./about.css"
import photo from '../images/me.jpg';
const AboutPage = ({id}) => (
        <>
          <div>
                <h1  className="about-header">About</h1>               
                <div  id={id} className="about">
                        <div id="about-left">
                             <img src={photo} alt={'Ph'}/>
                        </div>

                        <div id ="about-right">
                                <p>An undergraduate pursuing an advanced diploma in Computer Engineering Technology and Computing Science at Algonquin College with 2+ years of experience in software and web development.
                               <br/><br/> Strategic and self-confident, a lab technologist who analyzes complex problems and identify actionable processes.
                                <br/><br/>Academic experience in application development and testing in various platforms and technologies: Java, ReactJS, .Net and databases (MySQL and NoSQL).
                                <br/><br/> Currently focused on deep learning in AWS Solutions Architect, MERN Stack Architecture and networking through online courses.

                                </p>
                        </div>
                </div>
          </div>
        </>
);

export default AboutPage;