import React from 'react';
import './skills.css';



export default class SkillsPage extends React.Component{
    render(){
        return(
            <div>
                <h1>Skills</h1>
            <div id="skills" className="skills">
            <div className="skills-col">

                <div className="skillbarline">
                    <ul>
                        <li>100%</li>
                        <li>80%</li>
                        <li>60%</li>
                        <li>40%</li>
                        <li>20%</li>
                        
                    </ul>
                </div>
                <div className="skillbar java" data-percent="30%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar aws" data-percent="20%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar css" data-percent="30%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar react" data-percent="10%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar python" data-percent="40%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar nodejs" data-percent="20%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar c" data-percent="30%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar c2" data-percent="50%">
                    <div className="skillbar-bar"></div>
                </div>
                <div className="skillbar html5" data-percent="20%">
                    <div className="skillbar-bar"></div>
                </div>
                <div ></div>

            </div>
            <div className="skills-row">
                <ul>

                    <li>Java</li>
                    <li style={{marginLeft:"25px"}}>AWS</li>
                    <li style={{marginLeft:"25px"}}>CSS</li>
                    <li style={{marginLeft:"15px"}}>React</li>
                    <li >Python</li>
                    <li style={{marginLeft:"-10px"}}>NodeJS</li>
                    <li style={{marginLeft:"20px"}}>C</li>
                    <li style={{marginLeft:"38px"}}>C++</li>
                    <li style={{marginLeft:"15px"}}>HTML5</li>
                </ul>
            </div>
        </div>
        </div>
        );
    }
}